const filterPositioning = () => {
  const formFilters = document.querySelector('.form-filters');
  const minWidthDesktop = window.matchMedia('(min-width: 768px)');

  if (!formFilters) {
    return;
  }

  if (formFilters) {
    const filtersItem = formFilters.querySelectorAll('.filters-item');

    const positionsFilters = () => {
      filtersItem.forEach((item) => {
        const filtersList = item.querySelector('.filters-item__list');
        if (minWidthDesktop.matches) {
          let distanceBetween = formFilters.getBoundingClientRect().right - Math.round(item.getBoundingClientRect().left);
          let leftIndentItem = item.offsetLeft;
          let widthFiltersList = filtersList.offsetWidth;
          filtersList.style.left = leftIndentItem + 'px';

          if (distanceBetween <= widthFiltersList) {
            filtersList.style.left = 'auto';
            filtersList.style.right = 0;
          } else if (distanceBetween > widthFiltersList) {
            filtersList.style.right = 'auto';
            filtersList.style.left = leftIndentItem + 'px';
          }
        } else {
          filtersList.style.left = 'auto';
          filtersList.style.right = 'auto';
        }
      });
    };

    if (minWidthDesktop.matches) {
      setInterval(positionsFilters);
    }

    window.addEventListener('resize', () => {
      setInterval(positionsFilters);
    }, false);
  }
};

export {filterPositioning};
