const gallerySwipers = [];
window.gallerySwipers = gallerySwipers;

export const initGallerySliders = () => {

  const gallerySliders = document.querySelectorAll('.gallery-slider');

  if (!gallerySliders) {
    return;
  }

  gallerySliders.forEach((slider) => {
    const swiperNode = slider.querySelector('.swiper');
    const btnPrev = slider.querySelector('.swiper-button-prev');
    const btnNext = slider.querySelector('.swiper-button-next');
    // eslint-disable-next-line no-undef
    const swiper = new Swiper(swiperNode, {
      loop: true,
      slidesPerView: 3,
      spaceBetween: 30,
      navigation: {
        nextEl: btnNext,
        prevEl: btnPrev,
      },
      breakpoints: {
        0: {
          slidesPerView: 1,
        },
        540: {
          slidesPerView: 2,
        },
        1023: {
          slidesPerView: 2,
        },
        1279: {
          slidesPerView: 3,
        },
      },
    });
    gallerySwipers.push(swiper);
  });
};

