import {Accordions} from './accordions';
let accordions;

const initAccordions = () => {
  accordions = new Accordions();
  // Используйте в разработке экспортируемую переменную accordions, window сделан для бэкэнда
  window.accordions = accordions;
  const breakpointDesktop = window.matchMedia('(min-width:1280px)');
  const breakpointTablet = window.matchMedia('(max-width:1279px)');
  const sidebar = document.querySelector('.main-content__sidebar');

  const addsClassActive = () => {
    if (sidebar) {
      if (breakpointDesktop.matches) {
        sidebar.classList.add('is-active');
      } else if (breakpointTablet.matches) {
        sidebar.classList.remove('is-active');
      }
    }
  };

  if (sidebar) {
    addsClassActive();
    window.addEventListener('resize', () => {
      addsClassActive();
    }, false);
  }
};

export {initAccordions, accordions};
