const initFilterBar = () => {
  const filters = document.querySelector('.filters');
  const maxWidthMobile = window.matchMedia('(max-width: 767px)');

  if (!filters) {
    return;
  }

  if (filters) {
    const overlay = document.querySelector('.overlay');
    const filtersWrapper = document.querySelector('.filters__wrapper');
    const catalogSection = document.querySelector('.catalog-section');
    const filtersItem = document.querySelectorAll('[data-filter]');
    const formFiltersItem = document.querySelectorAll('.form-filters__item');
    const brandsItem = document.querySelectorAll('.filters-item__list .brands-item');
    const buttonFilters = filters.querySelector('.filters__btn');
    const buttonItem = filters.querySelectorAll('.filters-item__btn');

    let heightItems = 0;
    let heightBrands = 0;
    let heightButtons = 0;

    filtersItem.forEach((i) => {
      heightItems = heightItems + i.offsetHeight;
    });

    brandsItem.forEach((i) => {
      heightBrands = heightBrands + i.offsetHeight;
    });

    buttonItem.forEach((i) => {
      heightButtons = heightButtons + i.offsetHeight;
    });

    let heightFilters = heightItems + heightBrands + heightButtons;
    let heightWrapper;

    if (catalogSection) {
      heightWrapper = catalogSection.offsetHeight + heightFilters + 100;
    } else {
      heightWrapper = heightFilters + 100;
    }

    if (buttonFilters) {
      buttonFilters.addEventListener('click', () => {
        if (buttonFilters.classList.contains('filters__btn--active')) {
          buttonFilters.classList.remove('filters__btn--active');
          overlay.classList.remove('overlay--active');
          if (maxWidthMobile.matches) {
            filtersWrapper.style.maxHeight = null;
            formFiltersItem.forEach((item) => {
              const filtersList = item.querySelector('.filters-item__list');
              const filtersSublist = item.querySelector('.filters-item__sublist');
              item.classList.remove('filters__item--active');
              filtersList.style.maxHeight = null;
              if (filtersSublist) {
                filtersSublist.classList.remove('brands-sublist--active');
              }
            });
          }
        } else if (!buttonFilters.classList.contains('filters__btn--active')) {
          buttonFilters.classList.add('filters__btn--active');
          if (maxWidthMobile.matches) {
            filtersWrapper.style.maxHeight = heightWrapper + 'px';
          }
        }
      });
    }
  }
};

export {initFilterBar};
